<script>
  
     import __vue__ from 'vue';
     import {Button as __Button__,ButtonGroup as __ButtonGroup__,Col as __Col__,Row as __Row__,Step as __Step__,Steps as __Steps__} from 'element-ui';
     import {MyDescription as __MyDescription__,MyDetail as __MyDetail__,MyDetailItem as __MyDetailItem__,MyNumber as __MyNumber__,MyPanel as __MyPanel__,MyTable as __MyTable__,MyTitle as __MyTitle__,MyWrapper as __MyWrapper__} from '$ui';
     
     
     
     import MockForExample from '$my/code/mixin/mock-for-example'

  export default {
    mixins: [MockForExample],
    data() {
      return {
        links: [
          {
            label: '', value: 1
          },
          {
            label: '', value: 2
          }
        ],
        tabs: [
          {label: '', name: 'tab1'},
          {label: '', name: 'tab2'},
          {label: '', name: 'tab3'}
        ],
        columns: [
          {label: '', prop: 'name'},
          {label: '', prop: 'county'},
          {label: '', prop: 'created'},
          {label: '', prop: 'age'}
        ],
        list: []
      }
    },
    methods: {
      loadTabTable(tab) {
        this.fetchMockForExample({
          data: {
            tab: tab
          }
        }).then(res => {
          this.list = res.list
        })
      },
      handleTabChange(tab) {
        this.loadTabTable(tab)
      },
      handleLinkClick(link) {
        console.log(link)
      }
    },
    created() {
      this.loadTabTable('tab1')
    }
  }
     __vue__.use(__Button__);
__vue__.use(__ButtonGroup__);
__vue__.use(__Col__);
__vue__.use(__Row__);
__vue__.use(__Step__);
__vue__.use(__Steps__)
     __vue__.use(__MyDescription__);
__vue__.use(__MyDetail__);
__vue__.use(__MyDetailItem__);
__vue__.use(__MyNumber__);
__vue__.use(__MyPanel__);
__vue__.use(__MyTable__);
__vue__.use(__MyTitle__);
__vue__.use(__MyWrapper__)
    
  </script>

